import * as CANNON from 'cannon-es'
import Actor from "./Actor";
import InputController from "./controllers/InputController";
import PlayerFSM from './FSM/PlayerFSM';
import { math } from './utils/math';

class PlayerProxy {
  constructor(animations) {
    this._animations = animations;
  }

  get animations() {
    return this._animations;
  }
};

class Player extends Actor {
	constructor(name, world, target, position, animations) {
		super(name, world, target, position, new InputController());
		// Controller
		this._deceleration = new CANNON.Vec3(-100, 0, -10);
		this._acceleration = new CANNON.Vec3(150, 0, 150);
		this._velocity = new CANNON.Vec3(0, 0, 0);
		this._isGrounded = false;
		// Animation
		this._animations = animations;
		this._FSM = null;
		// Other
		//this._debugger = document.getElementById('debug-text');
		this.loaded = false;
		this._Init();
	}

  _Init(){
		this._target.body.position.set(this._position.x, this._position.y, this._position.z);
		this._FSM = new PlayerFSM(new PlayerProxy(this._animations));
		this._FSM.SetState('idle')
	}

	Update(delta) {
		// Velocity
		let velocity = this._velocity;
		let acc = this._acceleration;
		let frameDecceleration = new CANNON.Vec3(
			(velocity.x * this._deceleration.x),
			(velocity.y * this._deceleration.y),
			(velocity.z * this._deceleration.z)
		);
		frameDecceleration.x = Math.sign(frameDecceleration.x) * Math.min(Math.abs(frameDecceleration.x), Math.abs(velocity.x));
		frameDecceleration.z = Math.sign(frameDecceleration.z) * Math.min(Math.abs(frameDecceleration.z), Math.abs(velocity.z));
		velocity.x += frameDecceleration.x * (delta * 4);
		velocity.y += frameDecceleration.y * (delta * 4);
		velocity.z += frameDecceleration.z * (delta * 4);
		// Controlls
		const controlObject = this._target.body;
		if (this._controller._keys.forward && velocity.z > -40) {
			velocity.z -= acc.z * delta;
		}
		if (this._controller._keys.backward && velocity.z < 40) {
			velocity.z += acc.z * delta;
		}
		if (this._controller._keys.left && velocity.x > -40) {
			velocity.x -= acc.x * delta;
		}
		if (this._controller._keys.right && velocity.x < 40) {
			velocity.x += acc.x * delta;
		}
		// Movement
		let angle = Math.round(Math.atan2(velocity.x, velocity.z) * 180 / Math.PI) * delta
		let direction = new CANNON.Vec3(0, angle, 0);
		controlObject.velocity = velocity;
		controlObject.quaternion = math.EulerToQuaternion(direction.x, direction.y, direction.z)
		// Jump
		controlObject.addEventListener("collide", (e) => {
			if (e.body.material.name == "ground") {
				this._isGrounded = true;
			}
		})
		if (this._controller._keys.space && this._isGrounded) {
			controlObject.applyLocalImpulse(new CANNON.Vec3(0, 20, 0), this._zeroPoint);
			this._isGrounded = false
		}
		// FSM
		this._FSM.Update(delta, this._controller);
		//#region Debug
		// this._debugger.innerText = `
		// Velocity.x : (${Math.floor(velocity.x)}) \n  
		// Velocity.y : (${Math.floor(velocity.y)}) \n  
		// Velocity.z : (${Math.floor(velocity.z)}) \n 
		// Deceleration : (${Math.round(frameDecceleration.x)}, ${Math.round(frameDecceleration.y)}, ${Math.round(frameDecceleration.z)}) \n
		// Grounded : ${this._isGrounded} \n
		// `
		//#endregion
	}
}

export default Player;