import * as THREE from 'three';
import * as CANNON from 'cannon-es';
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader';
import {FBXLoader} from 'three/examples/jsm/loaders/FBXLoader';
import {threeToCannon,ShapeType} from 'three-to-cannon';
import CannonDebugRenderer from './Debug';
import Player from '../Player';
import { math } from '../utils/math';

const _VS = `

varying vec3 v_Normal;

void main(){
	gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
	v_Normal = normal;
}
`;

const _FS = `

uniform vec3 sphereColour;

varying vec3 v_Normal;

void main(){
	//gl_FragColor = vec4(v_Normal, 1.0);
	gl_FragColor = vec4(sphereColour, 1.0);
}
`;

class Physics {
	constructor(params) {
		this.params = params
		this._Initialize();
	}

	_Initialize() {
		this._world = new CANNON.World()
		this._world.gravity.set(0, -9.8, 0);
		this._debuger = new CannonDebugRenderer(this.params.scene, this._world);
		this._materials = {
			player: new CANNON.Material('player'),
			ground: new CANNON.Material('ground'),
			wall: new CANNON.Material('wall'),
			ball: new CANNON.Material('ball'),
		}
		this._contactBehaviour = {
			playerGround: new CANNON.ContactMaterial(this._materials.player, this._materials.ground, {
				friction: 0,
				restitution: 0
			}),
			playerWall: new CANNON.ContactMaterial(this._materials.player, this._materials.wall, {
				friction: 0,
				restitution: 0
			}),
			playerBall: new CANNON.ContactMaterial(this._materials.player, this._materials.ball, {
				friction: 0,
				restitution: 0
			}),
			groundBall: new CANNON.ContactMaterial(this._materials.ground, this._materials.ball, {
				friction: 1,
				restitution: 0.3,
			})
		}
		this._ApplyContactBehaviour();
	}

	_ApplyContactBehaviour() {
		for (const contact in this._contactBehaviour) {
			this._world.addContactMaterial(this._contactBehaviour[contact]);
		}
	}

	//#region Player
	AddPlayer() {
		//Body
		const shape = new CANNON.Box(new CANNON.Vec3(3.5, 10, 3));
		const body = new CANNON.Body({
			mass: 1,
			quaternion: new CANNON.Quaternion(0, 1, 0, 0),
			shape: shape,
			material: this._materials.player,
			fixedRotation: true,
		});
		// Scene/world
		const loader = new FBXLoader();
		loader.load('./src/models/man/man.fbx', (fbx) => {
			// Mesh
			fbx.traverse((obj) => {
				obj.castShadow = true
			})
			const mesh = fbx
			mesh.rotation.y = Math.PI
			mesh.scale.multiplyScalar(2)
			this.params.world.setPlayerMesh = mesh;
			this.params.scene.add(mesh);
			// Animations
			let animations = {}
			this._mixer = new THREE.AnimationMixer(mesh);
			this._manager = new THREE.LoadingManager();
			this._manager.onLoad = () => {
				this._world.addBody(body);
				this.params.world.setPlayerBody = body;
				this._CreatePlayer({mesh: mesh, body: body}, animations);
			}
			const _OnLoad = (animName, anim) => {
				const clip = anim.animations[0];
				const action = this._mixer.clipAction(clip);
				animations[animName] = {
					clip: clip,
					action: action
				}
			}
			const loader = new FBXLoader(this._manager);
			loader.setPath('./src/models/man/Animations/') 
			loader.load('idle.fbx', (a) => { _OnLoad('idle', a); });
			loader.load('run.fbx', (a) => { _OnLoad('run', a); });
		});
	}

	_CreatePlayer(target, anim) {
		const pos = new CANNON.Vec3(0, 15, 10);
		this.params.world.setPlayer = new Player('Human', this.params.world, target, pos, anim);
	}
	//#endregion

	AddBoard(params) {
		const loader = new GLTFLoader();
		loader.load('./src/models/lightboard/lightboard.gltf', (gltf) => {
			gltf.scene.traverse((obj) => {
				obj.scale.multiplyScalar(10);
			});
			// Physics
			const mesh = gltf.scene.children[0];
			mesh.castShadow = true;
			mesh.userData.interactive = true;
			mesh.userData.content = params.content;
			const shape = threeToCannon(mesh, {
				type: ShapeType.HULL
			});
			const body = new CANNON.Body({
				shape: shape.shape,
				mass: 0,
				position: new CANNON.Vec3(params.x, params.y, params.z),
				material: this._materials.wall
			});
			this._world.addBody(body);
			this.params.scene.add(mesh);
			// Spot Light
			const spotLight1 = new THREE.SpotLight( 0xffade1, 1.0, 20, Math.PI, 0, 1.0);
			const spotLight2 = new THREE.SpotLight( 0xffade1, 1.0, 20, Math.PI, 0, 1.0);
			// #1
			spotLight1.power = 10
			spotLight1.position.set(-1, 2.5, 0.5);
			spotLight1.target.position.set(-1, 3, 0)
			mesh.add(spotLight1);
			mesh.add(spotLight1.target);
			// #2
			spotLight2.power = 10
			spotLight2.position.set(1, 2.5, 0.5);
			spotLight2.target.position.set(1, 3, 0)
			mesh.add(spotLight2);
			mesh.add(spotLight2.target);
			// Sprite
			const map = new THREE.TextureLoader().load('./src/models/lightboard/press-e.png');
			const material = new THREE.SpriteMaterial( { map: map } );
			const sprite = new THREE.Sprite( material );
			sprite.scale.set(20.05, 5.15, 1);
			sprite.scale.multiplyScalar(0.12);
			sprite.position.set(1, 2, 1);
			sprite.visible = false;
			mesh.add(sprite);
			this.params.world.setObject = {mesh: mesh, body: body};
		});
	}

	AddSphere() {
		const mesh = new THREE.Mesh(
			new THREE.SphereGeometry(5, 32, 32),
			// new THREE.MeshPhongMaterial({
			// 	emissive: 0xff00a0,
			// 	emissiveIntensity: 1
			// })
			new THREE.ShaderMaterial({
				uniforms: {
					sphereColour : {
						value: new THREE.Vector3(0,0,1)
					}
				},
				vertexShader: _VS,
				fragmentShader: _FS
			})
		);
		mesh.castShadow = true;
		mesh.receiveShadow = true;
		const shape = new CANNON.Sphere(5);
		const body = new CANNON.Body({
			shape: shape,
			mass: 1,
			material: this._materials.ball,
			position: new CANNON.Vec3(0, 30, 0),
			angularDamping: 0.5
		});
		this._world.addBody(body);
		this.params.scene.add(mesh);
		this.params.world.setObject = {mesh: mesh, body: body};
	}

	AddBox(params) {
		const mesh = new THREE.Mesh(
			new THREE.BoxGeometry(params.x, params.y, params.z),
			new THREE.MeshStandardMaterial({
				color: params.color
			})
		);
		mesh.receiveShadow = true
		const shape = new CANNON.Box(new CANNON.Vec3(params.x / 2, params.y / 2, params.z / 2));
		const body = new CANNON.Body({
			shape: shape,
			mass: 0,
			material: this._materials.ground,
			position: new CANNON.Vec3(params.posX, params.posY, params.posZ)
		});
		this._world.addBody(body);
		this.params.scene.add(mesh);
		this.params.world.setObject = {
			mesh: mesh,
			body: body
		};
	}

	AddPlatform(params) {
		const loader = new GLTFLoader();
		loader.load('./src/models/platform/space_platform.gltf', (gltf) => {
			gltf.scene.traverse((obj) => {
				obj.scale.multiplyScalar(100);
			});
			// Physics
			const mesh = gltf.scene.children[0];
			mesh.receiveShadow = true
			const body = new CANNON.Body({
				//shape: shape.shape,
				shape: new CANNON.Box(new CANNON.Vec3(400, 12.8, 400)),
				mass: 0,
				position: new CANNON.Vec3(params.x, params.y, params.z),
				material: this._materials.ground
			});
			this._world.addBody(body);
			this.params.scene.add(mesh);
			this.params.world.setObject = {mesh: mesh, body: body};
		});
	}

	AddStatue(params){
		const loader = new GLTFLoader();
		loader.load('./src/models/statue/man_statue.gltf', (gltf) => {
			gltf.scene.traverse((obj) => {
				obj.scale.multiplyScalar(7);
			});
			// Physics
			const mesh = gltf.scene.children[0];
			mesh.castShadow = true;
			mesh.userData.interactive = true;
			mesh.userData.content = 'about';
			const body = new CANNON.Body({
				shape: new CANNON.Box(new CANNON.Vec3(15, 45, 15)),
				mass: 0,
				position: new CANNON.Vec3(params.x, params.y, params.z),
				material: this._materials.wall
			});
			this._world.addBody(body);
			this.params.scene.add(mesh);
			// Spot Light
			const spotLight1 = new THREE.SpotLight( 0xffade1, 1.0, 50, Math.PI / 4, 0.0, 1.0);
			const spotLight2 = new THREE.SpotLight( 0xffade1, 1.0, 50, Math.PI / 4, 0.0, 1.0);
			const spotLight3 = new THREE.SpotLight( 0xffade1, 1.0, 50, Math.PI / 4, 0.0, 1.0);
			const spotLight4 = new THREE.SpotLight( 0xffade1, 1.0, 50, Math.PI / 4, 0.0, 1.0);
			// #1
			spotLight1.power = 10
			spotLight1.position.set(-1.5, -2, 2);
			spotLight1.target.position.set(0, 1, 0)
			mesh.add(spotLight1);
			mesh.add(spotLight1.target);
			// #2
			spotLight2.power = 10
			spotLight2.position.set(1.5, -2, 2);
			spotLight2.target.position.set(0, 1, 0)
			mesh.add(spotLight2);
			mesh.add(spotLight2.target);
			// #3
			spotLight3.power = 10
			spotLight3.position.set(-1.5, -2, -2);
			spotLight3.target.position.set(0, 1, 0)
			mesh.add(spotLight3);
			mesh.add(spotLight3.target);
			// #4
			spotLight4.power = 10
			spotLight4.position.set(1.5, -2, -2);
			spotLight4.target.position.set(0, 1, 0)
			mesh.add(spotLight4);
			mesh.add(spotLight4.target);

			const map = new THREE.TextureLoader().load('./src/models/lightboard/press-e.png');
			const material = new THREE.SpriteMaterial( { map: map } );
			const sprite = new THREE.Sprite( material );
			sprite.scale.set(20.05, 5.15, 1);
			sprite.scale.multiplyScalar(0.12);
			sprite.position.set(2, -3, 3);
			sprite.visible = false;
			mesh.add(sprite);
			this.params.world.setObject = {mesh: mesh, body: body};
		});
	}

	AddLantern(params){
		const loader = new GLTFLoader();
		loader.load('./src/models/lanterns/lantern.gltf', (gltf) => {
			gltf.scene.traverse((obj) => {
				obj.scale.multiplyScalar(10);
			});
			// Physics
			const mesh = gltf.scene.children[0];
			mesh.castShadow = true;
			const body = new CANNON.Body({
				shape: new CANNON.Box(new CANNON.Vec3(3, 50, 3)),
				mass: 0,
				position: new CANNON.Vec3(params.x, params.y, params.z),
				material: this._materials.wall
			});
			var axis = new CANNON.Vec3(0,1,0);
			var angle = params.angle;
			body.quaternion.setFromAxisAngle(axis, angle);
			this._world.addBody(body);
			this.params.scene.add(mesh);
			// Spot Light
			const spotLight = new THREE.SpotLight( 0xffffff, 1.0, 100, Math.PI, 0.0, 1.0);
			spotLight.power = 20
			spotLight.position.set(0.5, 6.1, 0);
			mesh.add(spotLight);
			// Sprite
			this.params.world.setObject = {mesh: mesh, body: body};
		});
	}

	Update(delta) {
		this._world.step(delta);
		//this._debuger.update();
		this.params.world.Update();
		if(this._mixer){
			this._mixer.update(delta);
		}
	}
}

export default Physics;