class World{
	constructor(){
		this.world = {
			objects: [],
			player: {player: null, mesh: null, body: null},
		};
	}

	//#region Getters/Setters
	// Object get/set
	get getAllObjects(){
		return this.world.objects;
	}
	set setObject(object){
		this.world.objects.push(object);
	}
	// Player get/set
	get getPlayer(){
		return this.world.player;
	}
	set setPlayer(player){
		this.world.player.player = player;
	}
	set setPlayerMesh(mesh){
		this.world.player.mesh = mesh;
	}
	set setPlayerBody(body){
		this.world.player.body = body;
	}
	GetObject(name){
		for (let i = 0; i < this.world.objects.length; i++) {
			if(this.world.objects[i].mesh.userData === name){
				return this.world.objects[i];
			}
		}
		console.log(this.world.objects);
	}
	//#endregion

	Update(){
		this._UpdateObjects();
		this._UpdatePlayer();
	}

	_UpdateObjects(){
		for (let i = 0; i < this.world.objects.length; i++) {
			this.world.objects[i].mesh.position.x = this.world.objects[i].body.position.x;
			this.world.objects[i].mesh.position.y = this.world.objects[i].body.position.y;
			this.world.objects[i].mesh.position.z = this.world.objects[i].body.position.z;
			this.world.objects[i].mesh.quaternion.x = this.world.objects[i].body.quaternion.x;
			this.world.objects[i].mesh.quaternion.y = this.world.objects[i].body.quaternion.y;
			this.world.objects[i].mesh.quaternion.z = this.world.objects[i].body.quaternion.z;
			this.world.objects[i].mesh.quaternion.w = this.world.objects[i].body.quaternion.w;
		}
	}

	_UpdatePlayer(){
		if(this.world.player.body != null){
			this.world.player.mesh.position.x = this.world.player.body.position.x;
			this.world.player.mesh.position.y = this.world.player.body.position.y - 10;
			this.world.player.mesh.position.z = this.world.player.body.position.z;
			this.world.player.mesh.quaternion.x = this.world.player.body.quaternion.x;
			this.world.player.mesh.quaternion.y = this.world.player.body.quaternion.y;
			this.world.player.mesh.quaternion.z = this.world.player.body.quaternion.z;
			this.world.player.mesh.quaternion.w = this.world.player.body.quaternion.w;
		}

	}
}

export default World;