import * as THREE from 'three';
import Interaction from './components/Interaction';
import Physics from './components/physics/Physics';
import World from './components/World';
// Post Processing
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass';
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass';
import Dialog from './components/web-components/Dialog';

class Game {
	constructor() {
		this._canvas = document.getElementById("main-canvas");
		this._clock = new THREE.Clock();
		this._world = new World();
		this._dialog = new Dialog();
		this._Initialize();
	}

	_Initialize() {
		// Three.js setup
		this._threejs = new THREE.WebGLRenderer({
			canvas: this._canvas,
			antialias: true
		})
		this._threejs.shadowMap.enabled = true;
		this._threejs.shadowMap.type = THREE.PCFSoftShadowMap;
		this._threejs.setPixelRatio(window.devicePixelRatio);
		this._threejs.setSize(window.innerWidth, window.innerHeight);
		//this._threejs.setClearColor(0x06070E, 1);
		// Events
		window.addEventListener('resize', () => {
			this._OnWindowResize();
		}, false);
		window.addEventListener('mousemove', (e) => {
			this._OnMouseMove(e);
		}, false);
		// Camera setup
		const fov = 60;
		const aspect = window.innerWidth / window.innerHeight;
		const near = 1.0;
		const far = 1000.0;
		this._camOffset = new THREE.Vector3(0, 50, 100);
		this._camera = new THREE.PerspectiveCamera(fov, aspect, near, far);
		this._camera.position.set(this._camOffset);
		// Scene setup
		this._scene = new THREE.Scene();
		// Post processing
		this._composer = new EffectComposer(this._threejs);
		this._composer.addPass(new RenderPass(this._scene, this._camera));
		const bloomPass = new UnrealBloomPass({x: 1024, y: 1024}, 0.5, 0.0, 0.5);
		this._composer.addPass(bloomPass);
		// Light setup
		let light = new THREE.DirectionalLight(0xFFFFFF, 1);
		light.position.set(50, 100, 60);
		light.target.position.set(0, 0, 0);
		light.castShadow = true;
		light.shadow.bias = -0.001;
		light.shadow.mapSize.width = 2048;
		light.shadow.mapSize.height = 2048;
		light.shadow.camera.near = 0.1;
		light.shadow.camera.far = 500.0;
		light.shadow.camera.left = 100;
		light.shadow.camera.right = -100;
		light.shadow.camera.top = 100;
		light.shadow.camera.bottom = -100;
		this._light = light;
		this._lightTarget = light.target
		this._scene.add(this._light);
		this._scene.add(this._lightTarget);
		//Ambient light
		let ambLight = new THREE.AmbientLight(0x505050, 0.2);
		this._scene.add(ambLight);
		//Hemi light
		let hemi = new THREE.HemisphereLight(0x7A04EB, 0x120458, 1);
		this._scene.add(hemi);
		// Background
		const loader = new THREE.CubeTextureLoader();
    const texture = loader.load([
			'./src/models/skybox/posx.jpg',
			'./src/models/skybox/negx.jpg',
			'./src/models/skybox/posy.jpg',
			'./src/models/skybox/negy.jpg',
			'./src/models/skybox/posz.jpg',
			'./src/models/skybox/negz.jpg',
    ]);
    this._scene.background = texture;
		// Setup Physics
		this._physics = new Physics({
			scene: this._scene,
			world: this._world
		});
		this._StartPhysics();
		this._StartDialog();
		// Raycaster 
		this._raycaster = new THREE.Raycaster();
		this._mouse = new THREE.Vector2();
		// Board
		this._interactionController = new Interaction();
		// Update
		this._RAF();
	}

	_StartPhysics() {
		let platform = {x: -12.8, y: -12.8, z: 12.8};
		let board1 = {x: -250, y: 0, z: -150, content: 'km-panel'};
		let board2 = {x: -200, y: 0, z: -150, content: 'simargl'};
		let board3 = {x: -150, y: 0, z: -150, content: 'salong24'};
		let board4 = {x: -300, y: 0, z: -150, content: 'corona-counter'};
		let statue = {x: 0, y: 42, z: -20};
		let lantern1 = {x: 135, y: 0, z: -30, angle: -Math.PI / 2};
		let lantern2 = {x: -135, y: 0, z: -30, angle: -Math.PI / 2};
		this._physics.AddPlatform(platform);
		this._physics.AddBoard(board1);
		this._physics.AddBoard(board2);
		this._physics.AddBoard(board3);
		this._physics.AddBoard(board4);
		this._physics.AddStatue(statue);
		this._physics.AddLantern(lantern1);
		this._physics.AddLantern(lantern2);
		// Last
		this._physics.AddPlayer();
	}

	_StartDialog(){
		this._dialog.Open();
	}

	_RAF() {
		requestAnimationFrame(() => {
			this._delta = this._clock.getDelta();
			this._physics.Update(this._delta);
			// Player
			if (this._world.getPlayer.player != null) {
				this._world.getPlayer.player.Update(this._delta);
				this._camera.lookAt(this._world.getPlayer.body.position.x, this._world.getPlayer.body.position.y, this._world.getPlayer.body.position.z);
				this._camera.position.set(
					this._world.getPlayer.body.position.x + this._camOffset.x, 
					this._world.getPlayer.body.position.y + this._camOffset.y, 
					this._world.getPlayer.body.position.z + this._camOffset.z
				);
				this._light.target.position.set(this._world.getPlayer.body.position.x, 0, this._world.getPlayer.body.position.z);
				this._light.position.copy(this._light.target.position).add(new THREE.Vector3(50, 100, 60));
			}
			//#region Raycaster
			this._raycaster.setFromCamera(this._mouse, this._camera);
			const intersects = this._raycaster.intersectObjects(this._scene.children);
			// Check board
			if (intersects.length < 1){
				this._interactionController.Interaction(undefined, undefined);
			}
			for (let i = 0; i < intersects.length; i++) {
				if(intersects[0].object.userData.interactive){
					let board = intersects[0].object
					let sprite = null;
					for (let i = 0; i < intersects[0].object.children.length; i++) {
						if(intersects[0].object.children[i].type == 'Sprite'){
							sprite = intersects[0].object.children[i];
						}
					}
					this._interactionController.Interaction(board, sprite)
				} else {
					this._interactionController.Interaction(undefined, undefined);
				}
			}
			//#endregion
			// Render
			this._composer.render();
			this._RAF();
		})
	}

	_OnWindowResize() {
		this._camera.aspect = window.innerWidth / window.innerHeight;
		this._camera.updateProjectionMatrix();
		this._threejs.setSize(window.innerWidth, window.innerHeight);
	}

	_OnMouseMove(e) {	
		if(e.clientX != undefined || e.clientY != undefined){
			this._mouse.x = ( e.clientX / window.innerWidth ) * 2 - 1;
			this._mouse.y = - ( e.clientY / window.innerHeight ) * 2 + 1;
		}
	}
}

export default Game;