class Dialog{
	constructor(){
		this._dialog = document.getElementById('dialog');
		this._dialogBoxContent = document.getElementById('dialog-box-content');
		this._open = false;
		this._Init();
	}

	_Init(){
		this._dialog.addEventListener('click', () => {
			this.Close();
		})
	}

	Open(){
		this._dialog.style.display = "flex";
	}

	Close(){
		this._dialog.style.display = "none";
	}
}

export default Dialog;