import * as CANNON from 'cannon-es'

class Actor {
	constructor(name, world, target, position, controller) {
		this._name = name;
		this._world = world;
		this._target = target;
		this._position = position;
		this._controller = controller;
		this._zeroPoint = new CANNON.Vec3(0, 0, 0);
	}

	get getTarget(){
		return this._target;
	}

	get getPosition() {
		return this._target.body.position;
	}
}

export default Actor;