import InputController from "./controllers/InputController";
import Modal from "./web-components/Modal";

class Interaction{
	constructor(){
		this._input = new InputController()
		this._current = undefined;
		this._modal = new Modal();
	}

	//#region Board interaction
	Interaction(object, sprite){
		if(object != undefined){
			this._current = {object: object, sprite: sprite};
			this._current.sprite.visible = true;
			if(this._input._keys.select){
				this._Open(this._current.object.userData.content);
			}
			for (let i = 0; i < object.children.length; i++) {
				if(object.children[i].type == "SpotLight"){
					object.children[i].power = 15
				}
			}
		} 
		else if(object == undefined && this._current != undefined){
			this._current.sprite.visible = false;
			for (let i = 0; i < this._current.object.children.length; i++) {
				if(this._current.object.children[i].type == "SpotLight"){
					this._current.object.children[i].power = 10
				}
			}
		}
	}

	_Open(content){
		this._modal.Open(content);
	}

	_Close(){
		this._modal.Close();
	}
	//#endregion

}

export default Interaction